* {
    margin: 0;
    padding: 0;
    font-family: 'Courier New', Courier, monospace;
}
.flexBox {
    height: 100vh;
    width: auto;
    display: flex;
    flex-direction: column;
    background-image: url('../images/spanish-cantina.jpg');
    background-size: cover;
}
.navbar {
    height: 20%;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: rgb(70, 0, 0);
    color: rgb(209, 209, 101);
}
.navBtnYlw {
    height:35%;
    width: 10%;
    background-color: rgb(209, 209, 101);
    color: rgb(70, 0, 0);
    border-radius: 50%;
}
.body {
    height: 80%;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
}
.redBox {
    height: 20%;
    width: 15%;
    background-color: rgb(70, 0, 0);
    color: rgb(209, 209, 101);
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 2%;
    margin-bottom: 3%;
    opacity: 85%;
}
.boxBtnYlw {
    height:35%;
    width: 50%;
    background-color: rgb(209, 209, 101);
    color: rgb(70, 0, 0);
    border-radius: 15px;
}
.link {
    transition: all .5s ease;
    font-family:'Montserrat', sans-serif;
    text-transform: uppercase;
    text-align: center;
    line-height: 1;
    background-color : transparent;
    outline: none;
    font-size: 13px;
    text-decoration: none;
    color: rgb(70, 0, 0);
}